import { Component, Input } from '@angular/core';
import { environment } from "../../../environments/environment";

declare var AMap: any;

@Component({
  selector: 'stbui-amap',
  templateUrl: './amap.component.html',
  styleUrls: ['./amap.component.scss']
})
export class AmapComponent {

  @Input() width: string = '1000px';
  @Input() height: string = '500px';
  @Input() lat: number = 116.397428;
  @Input() lng: number = 39.90923;

  zoom:number = 11;
  id;
  isLoaded = false;

  constructor() {
    this.setMapId();
    this.getAmapStyles();

    this.loadScript(environment.amapApi);
  }

  setMapId() {
    let random = Math.floor(Math.random() * 100);
    this.id = `container_${random}`;
  }

  getAmapStyles() {
    return {
      width: this.width,
      heigiht: this.height
    };
  }

  loadScript(url: string) {
    let node = document.createElement('script');
    node.src = url;
    node.type = 'text/javascript';
    node.async = true;
    node.charset = 'utf-8';

    node.onload = () => {
      this.loadMap();
    };

    document.getElementsByTagName('head')[0].appendChild(node);
  }

  loadMap() {
    new AMap.Map(this.id, {
      resizeEnable: true,
      zoom: this.zoom,
      center: [this.lat, this.lng]
    });
  }

  geolocation() {
    let map = new AMap.Map('container');

    map.plugin('AMap.Geolocation', () => {
      let geolocation = new AMap.Geolocation({
        enableHighAccuracy: true,//:true
        timeout: 10000,          //10
        maximumAge: 0,           //00
        convert: true,           //true
        showButton: true,        //true
        buttonPosition: 'LB',    //'LB'
        buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
        showMarker: true,        //true
        showCircle: true,        //true
        panToLocation: true,     //true
        zoomToAccuracy: true      //false
      });
      map.addControl(geolocation);
    });
  }
}



