import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'datePicker'
})
export class DatePickerPipe implements PipeTransform {

  private localConfig = {
    dayAbbreviation: ['', '', '', '', '', '', ''],
    dayList: ['', '', '', '', '', '', ''],
    monthList: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'],
    monthLongList: ['', '', '', '', '', '', '', '', '', '', '', '']
  }


  transform(value: any, args?: any): any {
    if (args == 'undefined') args = 'formatMonth';
    //
    // console.log(args)
    // switch (args) {
    //   case 'formatMonth':
    //     return this.formatMonth(value);
    //   case 'formatDisplay':
    //     return this.formatDisplay(value);
    //   case 'formatDay':
    //     return this.formatDay(value);
    // }

    return this[args](value);
  }

  formatMonth(date) {
    return `${date.getFullYear()} ${this.localConfig.monthLongList[date.getMonth()]}`;
  }

  formatDisplay(date) {
    let day = date.getDate();
    return `${this.localConfig.monthList[date.getMonth()]}-${day > 9 ? day : '0' + day} ${this.localConfig.dayList[date.getDay()]}`;
  }

  formatDay(date) {
    return date.getDate();
  }
}



